.menuWrapper{
  margin-top: 100px;
  background-color: #ffdc8a;
  width: 500px;
  height:500px;
 border-radius: 40px;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;

}
button{
    cursor: pointer;
    width: 200px;
    height: 50px;
    border-radius: 30px;
    font-size:25px;
    box-shadow: 0px 0px 22px #ef8e3e;
    background-color: #ffa44d;
    border: none;
    transition: all 0.4s ease-in-out;
}
button:hover{
    box-shadow: 0px 0px 22px #de6300;
    background-color: #f68933;
}